.loginContainer {
    width: 100%;
    height: 80vh;
    display:flex;
    justify-content: center;
    align-items: center;
}

.loginForm {
    display: flex;
    flex-direction: column;
    padding: 30px 40px;
    justify-content: space-evenly;
    height: 50%;
    background-color:rgba(15, 69, 131, 0.829);
    border-radius:20px;
    box-shadow: -15px 10px 50px #4627b48e;
}

.loginForm input {
    background-color: transparent;
    border-top:none;
    border-left:none;
    border-right:none;
    font-weight: 600;
}

.loginForm input::placeholder {
    color: rgba(0, 0, 0, 0.479);
}

.loginForm input:focus {
    border-color:#b9cbd3d7;
    transition: .2s ease-in;
}


.button1 {
    outline: none; 
    margin-top: 4px;
    border: 1px solid;
    padding: 4px;
    background-color: transparent;
    color: rgb(137, 180, 19);
    transition:0.4s ease-in-out;
}

.button1:hover {
    color: white;
    border-color: transparent;
    background-color: rgb(19, 180, 19)
}