.plan__button {
    border: solid thin #5b0dda;
    background-color: #43108f;
    color: white;
    padding: 5px;
    font-weight: bold;
    outline: none;
}

.plan__button:hover{
    border: solid thin #5b0dda;
    background-color: transparent;
    color: #5b0dda;
    transition: .6s all;
}

.plan__area {
	padding: 4px;
	margin: 20px 0 0 0;
	font-family: cursive;
}

.plan__area__items {
	display: flex;
	flex-direction: row;
	width: 50%;
	margin-top: 10px;
	justify-content: space-between;
}

.plan__choise {
	border: solid 2px blue;
	padding: 2px;
	outline: none;
}

.plan__choise option {
	padding: 6px;
}

.plan__addItems {
	display: flex;
	flex-direction: column;
	height: 120px;
	width: 180px;
	justify-content: space-between;
}

.plan__addItems1 {
	display: flex;
	flex-direction: column;
	height: 120px;
	width: 180px;
	margin: 0 0 0 30px;
	justify-content: space-between;
}


.plan__showField {
	margin: 10px 0 10px 0;
}

.plan__ {
	display: flex;
	width: 100%;
	flex-direction: column;
	justify-content: space-evenly;
}