.updateCostumer {
    display: flex;
    flex-direction: row;
    height: 300px;
    padding: 5px;
}

.updateCostumer__part1 {
    flex: 0.3;
    margin: 16px;
    height: 100%;
    background-color:white;
    display: flex;
    padding:10px;
    flex-direction: column;
    justify-content: space-evenly;
}

.updateCostumer__part1 input {
    border: solid;
    border-width: thin;
}

.updateCostumer__part2 {
    margin: 10px 0 0 0dx;
}

.updateCostumers_searchbutton {
    margin: 10px 0 0 0;
