    border: 1px solid #eeccee;
}

.updateCostumers_th tr td {
    border: solid;
    border-width: thin;
    padding: 2px;
    font-weight: bold;
}

.updateCostumers_tb td {
    border: solid;
    border-width: thin;
    padding: 2px;
}

.updateCostumers__actionbutton {
    width:50px
}
.updateCostumers__actionbutton:hover {
