.collectionDetails__header {
	width: 100%;
	display: flex;
	flex-direction: row;
	padding: 10px;
	background-color: #f50057;
	border-radius: 10px;
}

.collectionDetails__dateInput {
	display: flex;
}

.collectionDetails__dl {
	margin: 0 0 0 20px;
}

.collectionDetails__body {
	display: flex;
	width: 100%;
}

.collectionDetails__collectionTable1 {
	overflow-y: auto;
	flex: 0.6;
	height: auto;
	margin-top: 20px;
	justify-content: space-evenly;
}

.collectionDetails__collectionTable table thead tr th {
	width: 100%;
	position: sticky;
	top: 0;
	border: solid thin #f50057;
}

.collectionDetails__collectionTable table {
	border-collapse: collapse;
	width: 55%;
}

th,
td {
	padding: 8px 16px;
	border: 1px solid #ccc;
}

th {
	background: #eee;
}

.collectionDetails__collectiontr tr td {
	padding: 3px;
	border: solid thin;
}

.collectionDetails__collectionform {
	margin: 20px;
	flex: 0.2;
	display: flex;
	flex-direction: column;
	height: 250px;
	justify-content: space-around;
}
