

.collectionDetails__header {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 10px;
    background-color: #b8f0c4;
    border-radius: 10px;
}


.collectionDetails__body {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.collectionDetails__collectionTable {
    overflow-y: auto;
    height: 400px;
    flex: 0.4;
    margin-top: 20px;
    justify-content: space-evenly;
}

.collection_name_ {
    width: 100px;
}

.collectionDetails__collectionTable table thead tr th {
    width: 100%;
    position: sticky;
    top: 0;
    border: solid thin black;
}

.collectionDetails__collectionTable table  {
  border-collapse: collapse;
  width: 55%;
}

th,
td {
    padding: 8px 16px;
    border: 1px solid #ccc;
}

th {
    background: rgb(0, 0, 0);
}


.collectionDetails__collectionTableBody td {
    padding: 5px;
    border: solid thin black;
}

.collectionDetails__collectiontr tr td {
    padding: 3px;
    border: solid thin;
}



.collectionDetails__input {
    border: double;
    outline: none;
    padding: 2px;
}
